import React, { useState, useRef, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { useTranslation } from "react-i18next";
import ApiVideoPlayer from "@api.video/react-player";

const ReelItem = ({ video, isActive, onVideoEnd, onVideoClick }) => {
  const [isPaused, setIsPaused] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [progress, setProgress] = useState(0);
  const [isLiked, setIsLiked] = useState(false);
  const [showAction, setShowAction] = useState(false);
  const [isReady, setIsReady] = useState(false);
  const playerRef = useRef(null);
  const interval = useRef(null);
  const wasActive = useRef(isActive);
  const { t, i18n } = useTranslation();
  const isRTL = i18n.language === "ar";

  // Handle video progress bar
  useEffect(() => {
    if (isActive && !isPaused && isReady) {
      interval.current = setInterval(() => {
        if (playerRef.current) {
          const player = playerRef.current;
          const duration = player.getDuration ? player.getDuration() : 1;
          const currentTime = player.getCurrentTime
            ? player.getCurrentTime()
            : 0;
          setProgress((currentTime / duration) * 100);

          if (duration > 0 && currentTime >= duration - 0.2) {
            onVideoEnd();
          }
        }
      }, 100);
    }

    return () => {
      if (interval.current) clearInterval(interval.current);
    };
  }, [isActive, isPaused, onVideoEnd, isReady]);

  // Autoplay when active, pause when inactive
  useEffect(() => {
    if (playerRef.current && isReady) {
      // Only play if component is active and not paused
      if (isActive && !isPaused) {
        playerRef.current.play && playerRef.current.play();
      } else {
        playerRef.current.pause && playerRef.current.pause();
      }

      // Track when a video becomes inactive after being active
      if (wasActive.current && !isActive) {
        playerRef.current.pause && playerRef.current.pause();
      }

      // Reset video when it becomes inactive
      if (!isActive) {
        playerRef.current.seekTo && playerRef.current.seekTo(0);
        setProgress(0);
      }

      // Update wasActive ref
      wasActive.current = isActive;
    }

    // When component becomes active, set loading to false after a delay
    if (isActive && isReady) {
      const timer = setTimeout(() => {
        setIsLoading(false);
      }, 500);
      return () => clearTimeout(timer);
    }
  }, [isActive, isPaused, isReady, video.id]);

  const togglePlay = (e) => {
    e.stopPropagation();
    setIsPaused(!isPaused);
  };

  const toggleLike = (e) => {
    e.stopPropagation();
    setIsLiked(!isLiked);
    setShowAction(true);
    setTimeout(() => setShowAction(false), 1500);
  };

  // Determine if we should show the play button (when paused OR not active)
  const shouldShowPlayButton = isPaused || !isActive;

  return (
    <motion.div
      className="relative w-full h-full snap-start flex-shrink-0 group"
      onClick={onVideoClick}
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      {/* Loading indicator */}
      {isLoading && (
        <div className="absolute inset-0 flex items-center justify-center z-10 bg-black">
          <div className="relative w-12 sm:w-16 h-12 sm:h-16">
            <div className="absolute inset-0 rounded-full border-4 border-gray-300/30 border-t-primary animate-spin"></div>
            <div className="absolute inset-0 flex items-center justify-center">
              <motion.div
                className="w-8 sm:w-10 h-8 sm:h-10 bg-primary/20 rounded-full"
                animate={{ scale: [1, 1.2, 1] }}
                transition={{
                  duration: 1.5,
                  repeat: Infinity,
                  ease: "easeInOut",
                }}
              />
            </div>
          </div>
        </div>
      )}

      {/* Video */}
      <motion.div
        className="w-full h-full overflow-hidden"
        initial={false}
        animate={isActive ? { scale: 1 } : { scale: 0.98 }}
        transition={{ duration: 0.3 }}
      >
        <div className="w-full h-full">
          {video.videoId && (
            <ApiVideoPlayer
              ref={playerRef}
              onReady={() => {
                setIsReady(true);
                setIsLoading(false);
              }}
              hideTitle
              video={{ id: video.videoId }}
              autoplay={isActive}
              style={{
                width: "100%",
                height: "100%",
              }}
            />
          )}
        </div>
      </motion.div>

      {/* Play/Pause overlay - Only show when video is paused or when not active */}
      {shouldShowPlayButton && (
        <div
          className="absolute inset-0 flex items-center justify-center z-20 cursor-pointer"
          onClick={togglePlay}
        >
          <motion.div
            initial={{ opacity: 0, scale: 0.8 }}
            animate={{ opacity: 1, scale: 1 }}
            className="w-16 h-16 sm:w-20 sm:h-20 bg-black/40 backdrop-blur-md rounded-full flex items-center justify-center"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-8 w-8 sm:h-10 sm:w-10 text-white"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M14.752 11.168l-3.197-2.132A1 1 0 0010 9.87v4.263a1 1 0 001.555.832l3.197-2.132a1 1 0 000-1.664z"
              />
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
              />
            </svg>
          </motion.div>
        </div>
      )}

      {/* Progress bar */}
      <div className="absolute top-0 left-0 right-0 h-1 sm:h-1.5 bg-gray-700/40 backdrop-blur-sm z-10">
        <motion.div
          className="h-full bg-gradient-to-r from-primary via-primary/90 to-primary"
          style={{ width: `${progress}%` }}
          initial={{ width: "0%" }}
          animate={{ width: `${progress}%` }}
          transition={{ duration: 0.1 }}
        />
      </div>

      {/* Gradient overlay */}
      <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/20 to-black/30 pointer-events-none" />

      {/* Subtle shine effect */}
      <motion.div
        className="absolute inset-0 bg-gradient-to-tr from-transparent via-white/5 to-transparent pointer-events-none"
        animate={{
          opacity: [0.2, 0.3, 0.2],
        }}
        transition={{
          duration: 5,
          repeat: Infinity,
          ease: "easeInOut",
        }}
      />

      {/* Like Action Animation */}
      <AnimatePresence>
        {showAction && (
          <motion.div
            className="absolute inset-0 flex items-center justify-center pointer-events-none"
            initial={{ opacity: 0, scale: 0.5 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 1.5 }}
            transition={{ duration: 0.5 }}
          >
            <motion.div
              className="text-red-500"
              animate={{ scale: [1, 1.5, 1] }}
              transition={{ duration: 0.5 }}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-16 w-16 sm:h-24 sm:w-24 fill-red-500"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={1}
                  d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z"
                />
              </svg>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Video info */}
      <motion.div
        className={`absolute bottom-3 ${
          i18n.language === "ar" ? "right-2" : "left-2"
        } max-w-[70%] z-10`}
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.2, duration: 0.5 }}
      >
        <div className="flex items-center space-x-2 ">
          <h3 className="text-base sm:text-lg font-bold text-white drop-shadow-md">
            {video.title}
          </h3>
        </div>
        <p className="text-xs sm:text-sm text-white/90 mb-2 line-clamp-2 drop-shadow-md">
          {(video.description || "").length > 20
            ? `${(video.description || "").substring(0, 20)}...`
            : (video.description || "")}
        </p>
        {video.course_id && (
          <div className="flex items-center">
            <motion.button
              whileHover={{
                scale: 1.05,
                backgroundColor: "rgb(var(--color-primary-600))",
              }}
              whileTap={{ scale: 0.95 }}
              className="bg-primary text-white px-3 py-1 rounded-full text-xs font-medium shadow-lg shadow-primary/20 backdrop-blur-sm border border-white/10 flex items-center space-x-1 hidden group-hover:block transition-opacity duration-300"
              onClick={(e) => {
                e.stopPropagation();
                window.location.href = `/course/${video.course_id}`;
              }}
            >
              <span>{t("home.watch_course")}</span>
            </motion.button>
          </div>
        )}
      </motion.div>

      {/* Right side controls */}
      <div
        className={`absolute bottom-4 sm:bottom-8 ${
          isRTL ? "left-2 sm:left-3" : "right-2 sm:right-3"
        } flex flex-col items-center space-y-3 sm:space-y-5 z-10`}
      >
        <motion.button
          whileHover={{ scale: 1.1, backgroundColor: "rgba(0,0,0,0.4)" }}
          whileTap={{ scale: 0.9 }}
          onClick={toggleLike}
          className="w-9 h-9 sm:w-12 sm:h-12 rounded-full bg-black/30 backdrop-blur-md flex flex-col items-center justify-center text-white border border-white/10 shadow-lg"
        >
          <AnimatePresence mode="wait">
            {isLiked ? (
              <motion.svg
                key="liked"
                initial={{ scale: 0.5, opacity: 0 }}
                animate={{ scale: 1, opacity: 1 }}
                exit={{ scale: 0.5, opacity: 0 }}
                className="h-4 w-4 sm:h-5 sm:w-5 text-red-500 fill-red-500"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z"
                />
              </motion.svg>
            ) : (
              <motion.svg
                key="unliked"
                initial={{ scale: 0.5, opacity: 0 }}
                animate={{ scale: 1, opacity: 1 }}
                exit={{ scale: 0.5, opacity: 0 }}
                className="h-4 w-4 sm:h-5 sm:w-5 text-white"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z"
                />
              </motion.svg>
            )}
          </AnimatePresence>
          <motion.span
            className="text-[10px] sm:text-xs mt-0.5 sm:mt-1"
            animate={isLiked ? { scale: [1, 1.3, 1] } : {}}
            transition={{ duration: 0.3 }}
          >
            {isLiked ? (video.views || 0) + 1 : (video.views || 0)}
          </motion.span>
        </motion.button>

        <motion.button
          whileHover={{ scale: 1.1, backgroundColor: "rgba(0,0,0,0.4)" }}
          whileTap={{ scale: 0.9 }}
          className="w-9 h-9 sm:w-12 sm:h-12 rounded-full bg-black/30 backdrop-blur-md flex items-center justify-center text-white border border-white/10 shadow-lg"
          onClick={(e) => {
            e.stopPropagation();
            const shareUrl = video.course_id
              ? `/course/${video.course_id}`
              : window.location.pathname;
            navigator
              .share({
                title: video.title,
                text: video.description || "",
                url: shareUrl,
              })
              .catch(() => {
                // Share API not supported or failed
                navigator.clipboard.writeText(
                  window.location.origin + shareUrl
                );
              });
          }}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-4 w-4 sm:h-5 sm:w-5"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M8.684 13.342C8.886 12.938 9 12.482 9 12c0-.482-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l6.632 3.316m-6.632-6l6.632-3.316m0 0a3 3 0 105.367-2.684 3 3 0 00-5.367 2.684zm0 9.316a3 3 0 105.368 2.684 3 3 0 00-5.368-2.684z"
            />
          </svg>
        </motion.button>
      </div>
    </motion.div>
  );
};

export default ReelItem;
